.modal-intro {
    position: absolute;
    background: #000000ab;
    top: 0;
    height: 100%;
    width: 100%;
}

.modal-wrapper {
    background: white;
    border: 1px solid #d0cccc;
    box-shadow: 0 5px 8px 0 rgba(0,0,0,0.2), 0 7px 20px 0 rgba(0,0,0,0.17);
    margin: 100px auto 0;
    transition: all .8s;
    width: 60%;
    border-radius: 10px;
    }
     
    .modal-header {
    background: #e2e2e2;
    height: 40px;
    line-height: 40px;
    padding: 15px 20px;
    text-align: center;
    border-radius: 5px;
    }
     
    .modal-header h2 {
    margin: 0;
    padding: 0;
    }
     
    .modal-body {
    padding: 20px 15px;
    text-align: center;
    }

    .modal-body img {
        max-width: 200px;
    }
     
    .modal-footer {
    background: #e2e2e2;
    height: 35px;
    padding: 15px;
    }
     
    .close-modal-btn {
    color: #34495e;
    cursor: pointer;
    font-size: 30px;
    margin: 0;
    position: absolute;
    right: 15px;
    top: 0;
    }
     
    .close-modal-btn:hover {
    color: black;
    }
     
    .btn-cancel {
    background-color: #b71c1c;
    float: left;
    }
     
    .btn-continue {
    background-color: #1b5e20;
    float: right;
    }
     
    .back-shed {
    background-color: rgba(48, 49, 48, 0.42);
    height: 100%;
    position: fixed;
    transition: all 1.3s;
    width: 100%;
    }
     
    .open-modal-btn {
    margin: 15px;
    padding: 10px;
    }